

















































































































































































.modify {
  width: 100vw;
  height: 100vh;
  background-color: #f9f9f9;
  .modifyTop {
    width: 100vw;
    height: 9vh;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    display: flex;
    align-items: center;
    img:nth-child(1) {
      width: 7vw;
      height: 7vw;
      margin-left: 5vw;
    }
    img:nth-child(3) {
      width: 7vw;
      height: 7vw;
      margin-left: 28vw;
    }
    div {
      margin-left: 25vw;
      font-size: 6.5vw;
      color: white;
    }
  }
  .modifyBox {
    width: 95vw;
    margin: 0 auto;
    padding-top: 8vw;
    box-sizing: border-box;
    height: 91vh;
    .modifyBoxTitle {
      padding: 1vw;
      font-weight: bold;
      font-size: 4.7vw;
      background-color: white;
    }
    .modifyItem {
      margin-top: 4vw;
      font-size: 4.8vw;
      color: #a6a6a6;
      padding: 2.5vw;
      border: 0.1vw solid #a6a6a663;
      border-radius: 1vw;
      .modifyName {
        margin-bottom: 3vw;
        .name {
          margin-left: 5vw;
        }
      }
      .modifyPassword {
        width: 100%;
        font-weight: bold;
      }
    }
    .modifyText {
      color: #d6d6d6;
      font-size: 3.8vw;
      font-weight: bold;
      padding: 2.5vw;
    }
    .ForgetPassword {
      color: #5a9aff;
      font-size: 3.8vw;
      font-weight: bold;
      padding: 0 2vw 2vw;
    }
    .SubmitBtn {
      margin-top: 70vw;
      height: 5vh;
      line-height: 5vh;
      text-align: center;
      background-color: #15cfa4;
      font-size: 4.7vw;
      color: white;
    }
  }
}
